@import '../../vars.css';

.container {
    color: var(--color-text);
    margin-top: 79px;
    margin-left: 10%;
    margin-right: 10%;

}

.title {
    color: var(--color-text);
    font-size: 35px;
    font-weight: 700;
    letter-spacing: 1.75px;
}

.content {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 14px;
}

.skills {
    width: 48%;
    display: flex;
    flex-wrap: wrap;
    gap: 37px;
}

.skill {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* gap: 1px; */
}

.skillImageContainer {
    /* background-color: var(--color-secondary); */

    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 130px;
    height: 130px;
}

.skillImageContainer img {
    width: 90px;
}

.skill p {
    font-size: 23px;
    font-family: var(--font-roboto);
    font-weight: 400;
}

.history {
    width: 52%;
    display: flex;
    flex-direction: column;
    gap: 40px;

}

.historyItem {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 17px;
    /* background: #19376d;
    background: linear-gradient(90deg, #19376d 0%, rgba(25, 55, 109, 0) 100%); */
    background: #19376d;
    background: linear-gradient(90deg, #f5d849 0%, rgba(25, 55, 109, 0) 100%);
    border-radius: 10px;
    padding: 24px;

}



.historyItem img {
    width: 50px;
}

.historyItemDetails {
    font-family: var(--font-roboto);
}

.historyItemDetails h3 {
    font-size: 27px;
    font-weight: 500;
    font-family: var(--font-roboto);
}

.historyItemDetails p {
    font-size: 18px;
    font-weight: 200;
    font-family: var(--font-roboto);
}

.historyItemDetails ul {
    margin-top: 6px;
    list-style-position: outside;
    font-size: 22px;
    list-style-type: disc;
    margin-left: 12px;



}


@media screen and (max-width: 830px) {
    .content {
        flex-direction: column;
        align-items: center;
        gap: 34px;
    }

    .skills {
        width: 100%;
        flex-direction: row;
        justify-content: center;
    }

    .history {
        width: 100%;
        gap: 9px;
    }
}